import Vue from "vue";
import Vuex from "vuex";

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    spreadsheet: {
      id: "",
      url: "",
      title: ""
    },
    sheet: {
      title: ""
    },
    product: "",
    template: "",
    document: "",
    mergefields: [] as string[],
    location: (window._app && window._app.location) || "/"
  },

  mutations: {
    setSpreadsheet(state, newValue) {
      if (!newValue || !newValue.id) {
        return;
      }

      state.spreadsheet.id = newValue.id;
      state.spreadsheet.url = newValue.url;
      state.spreadsheet.title = newValue.title;
    },

    clearSheet(state) {
      state.sheet.title = "";
    },

    setSheet(state, newValue: string) {
      state.sheet.title = newValue;
    },

    setProduct(state, newValue: string) {
      state.product = newValue;
    },

    setTemplate(state, newValue: string) {
      state.template = newValue;
    },

    setDocument(state, newValue) {
      state.document = newValue;
    },

    setMergefields(state, newValue: string[]) {
      state.mergefields = newValue;
    },

    setLocation(state, newValue: string) {
      state.location = newValue;
    }
  },

  actions: {
    initializeFromUserLatestMerge(context) {
      const userLatestMerge = window._user && window._user.latest;

      const product = userLatestMerge && userLatestMerge.product;
      const template = userLatestMerge && userLatestMerge.template;

      if (product && template) {
        context.commit("setProduct", product || "");
        context.commit("setTemplate", template || "");
      }
    },

    initializeFromAppContext(context) {
      const appContext = window._app && window._app.context;

      const spreadsheet = appContext && appContext.spreadsheet;
      const sheet = appContext && appContext.sheet;

      if (spreadsheet) {
        context.dispatch("setSpreadsheet", spreadsheet);
      }

      if (sheet) {
        context.dispatch("setSheet", sheet.title);
      }
    },

    setSpreadsheet: function(context, newValue) {
      context.commit("setSpreadsheet", newValue);
    },

    clearSheet: function(context) {
      context.commit("clearSheet");
    },

    setSheet: function(context, newValue: string) {
      context.commit("setSheet", newValue);
      context.dispatch("getMergefields");
    },

    setProduct(context, newValue) {
      context.commit("setProduct", newValue);
    },

    setTemplate(context, newValue) {
      context.commit("setTemplate", newValue);
    },

    setDocument(context, newValue) {
      context.commit("setDocument", newValue);
    },

    getMergefields(context) {
      const success = function(data: string[]) {
        context.dispatch("setMergefields", data);
      };

      const spreadsheetId = context.state.spreadsheet.id;
      const sheetName = context.state.sheet.title;

      context.commit("setMergefields", []);
      window._backend.withSuccessHandler(success).uiGetSheetHeaders(spreadsheetId, sheetName);
    },

    setMergefields(context, data: string[]) {
      context.commit("setMergefields", data);
    },

    setLocation(context, newValue) {
      context.commit("setLocation", newValue);
    }
  }
});
