


































import Vue from "vue";
import { mapState } from "vuex";

export default Vue.extend({
  name: "CreateLabelsDesign",
  data() {
    return {
      mergefield: "" as string
    };
  },
  computed: {
    ...mapState(["mergefields"]),
    template: {
      get(): string {
        return this.$store.state.template;
      },
      set(newValue): void {
        this.$store.dispatch("setTemplate", newValue);
      }
    }
  },
  methods: {
    addMergeField(e: MouseEvent) {
      const target = (e.target ? e.target : e.srcElement) as HTMLSelectElement;
      const value = target.value;

      let newContent = this.template as string;
      if (newContent.slice(-2) === ">>") {
        newContent += " ";
      }
      newContent += "<<" + value + ">>";

      this.template = newContent;
      this.mergefield = "";

      const box = document.getElementById("labelmaker-template");
      if (box) {
        box.focus();
      }
    }
  }
});
